* {
  margin: 0;
  padding: 0;
}
body,
html {
  width: 100%;
  height: 100%;
  background: #eee;
}
* {
  box-sizing: border-box;
  font-size: 14px;
}
ul li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #333;
}
#app {
  width: 100%;
  height: 100%;
}
.App {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  padding: 60px 0 0 140px;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.clearFix:after {
  content: '.';
  display: block;
  height: 0;
  visibility: hidden;
  clear: both;
}
.clearFix {
  zoom: 1;
}
/*IE6,7触发haslayout*/
.txtleft {
  text-align: left;
}
.txtright {
  text-align: right;
}
.txtcenter {
  text-align: center;
}
.flex {
  display: flex;
}
.flexitem {
  flex: 1;
  justify-content: center;
  align-items: center;
}
.hide {
  display: none;
}
.show {
  display: block;
}
