.btn {
  background: #ffc900;
  color: #fff;
  border: 0;
  padding: 5px 15px;
  border-radius: 2px;
}
.Header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background: #fff;
  z-index: 2;
}
.Header .title {
  color: #333;
  height: 60px;
  line-height: 60px;
  position: relative;
}
.Header .title .username {
  cursor: pointer;
}
.Header .title .opeartion {
  box-shadow: 8px 8px 8px #ccc;
  background: #fff;
  width: 120px;
  position: absolute;
  top: 70px;
  right: 20px;
}
.Header .title .opeartion .trangle {
  position: absolute;
  top: -8px;
  right: 30px;
  display: inline-block;
  border-width: 0 10px 10px;
  border-style: solid;
  border-color: transparent transparent #fff;
}
.Header .title .opeartion li {
  cursor: pointer;
  background: #fff;
  text-align: left;
  padding: 0 20px;
  height: 46px;
  line-height: 46px;
}
.Header .title .opeartion li .iconfont {
  margin-right: 5px;
}
.Header .title .logo {
  display: inline-block;
  width: 140px;
  height: 40px;
  vertical-align: middle;
  z-index: 1;
  position: relative;
  background: url(img/logo.c5860eee.jpg) no-repeat center;
  background-size: contain;
}
.Menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 140px;
  background: #fff;
  height: 100vh;
  padding-top: 70px;
  color: #333;
  z-index: 1;
}
.Menu ul li {
  line-height: 36px;
  text-align: left;
  cursor: pointer;
}
.Menu ul li .iconfont {
  margin-right: 8px;
  margin-left: 15px;
}
.Menu ul li .youjiantou {
  float: right;
}
.Menu ul li .rotate {
  transform: rotate(90deg);
}
.Menu ul li .SubItem {
  background: #fafafa;
}
.Menu ul li .item {
  text-align: center;
  background: #fafafa;
}
.Menu ul li .item a {
  font-size: 12px;
}
.Menu ul li .item:hover {
  background: #fff0ac;
  border-left: 3px solid #ffc900;
}
.ErrPage {
  width: 100%;
  height: 100%;
  background: rgba(255, 0, 0, 0.3);
  padding-top: 100px;
}
.Home .welcome {
  margin-top: 100px;
}
.bgcon {
  background: #fff;
  margin: 15px;
  padding: 15px;
}
.input-wrap {
  display: flex;
  float: left;
  margin: 5px 0;
}
.input-wrap label {
  width: 80px;
  text-align: right;
  line-height: 32px;
}
.input-wrap .input {
  border: 1px solid #d8d8d8;
  height: 32px;
  margin-right: 10px;
}
.table {
  width: 100%;
  border-collapse: collapse;
}
.table tr th {
  padding: 8px 0;
  border: 1px solid #d8d8d8;
}
.table tr td {
  padding: 8px 0;
  border: 1px solid #d8d8d8;
}
.nav {
  margin-top: 15px;
  text-align: left;
  margin-left: 15px;
}
